import * as React from 'react'
import FacetNavPanel from './FacetNavPanel'
import { applyChangesToValuesColumn } from '../query-filter/QueryFilter'
import { QueryWrapperChildProps } from 'lib/containers/QueryWrapper'
import {
  FacetColumnResultValues,
  FacetColumnResultValueCount,
  FacetColumnRequest,
  FacetColumnResult,
} from 'lib/utils/synapseTypes'
import { useState, useEffect } from 'react'
import * as _ from 'lodash'

export type FacetNavProps = {
  applyChanges?: Function
  loadingScreen?: React.FunctionComponent | JSX.Element
  facetsToPlot?: string[] | undefined
}

type UiFacetState = {
  name: string
  hidden: 1 | 0
  isExpanded?: boolean
  index?: number
}

type ExpandedFacet = {
  facet: FacetColumnResult
  index: number
}

type InternalProps = FacetNavProps & QueryWrapperChildProps

const FacetNav: React.FunctionComponent<InternalProps> = ({
  data,

  getLastQueryRequest,
  isLoadingNewData,
  loadingScreen,
  executeQueryRequest,
  asyncJobStatus,
}: InternalProps): JSX.Element => {
  const [isShowAll, setIsShowAll] = useState(false)

  const [facets, setFacets] = useState<FacetColumnResult[]>([])
  const [facetUiStateArray, setFacetUiStateArray] = useState<UiFacetState[]>([])
  const [expandedFacets, setExpandedFacets] = useState<ExpandedFacet[]>([])

  const request = getLastQueryRequest!()

  const applyChangesFromQueryFilter = (facets: FacetColumnRequest[]) => {
    request.query.selectedFacets = facets
    executeQueryRequest!(request)
  }
  useEffect(() => {
    //scenario 1 - on load -- return 4 and fill other
    //scenario 2 - on show all
    let result = data?.facets?.filter(item => item.facetType === 'enumeration')
    if (!result) {
      return
    }

    if (!isShowAll) {
      // the first time we run we want to set an array
      if (facetUiStateArray.length === 0) {
        setFacetUiStateArray(
          result.map(item => ({
            name: item.columnName,
            hidden: 0,
          })),
        )

        setFacets(result?.slice(0, 4))
      } else {
        setFacetUiStateArray(
          facetUiStateArray.map((item, index) =>
            index >= 4 ? { ...item, hidden: 1 } : { ...item },
          ),
        )
      }
    } else {
      //first show all
      if (facets.length < result.length) {
        setFacets(result)
      } else {
        setFacetUiStateArray(
          facetUiStateArray.map(item => ({ ...item, hidden: 0 })),
        )
      }
    }
  }, [data, isShowAll])

  const hideExpandedItem = (facet: FacetColumnResult, index:number) => {
    toggleExpand(facet, index, false)
    hideItem(facet.columnName)

  }
  const toggleExpand = (
    facet: FacetColumnResult,
    index: number,
    doExpand: boolean,
  ) => {
    setProperty(facet.columnName, 'isExpanded', doExpand)
    if (doExpand) {
      setExpandedFacets([...expandedFacets, { facet, index }])
    } else {
      setExpandedFacets(
        expandedFacets.filter(
          item => item.facet.columnName != facet.columnName,
        ),
      )
    }
  }

  const hideItem = (columnName: string) => {
    setProperty(columnName, 'hidden', 1)

  }

  const setProperty = (
    columnName: string,
    propName:  keyof UiFacetState,
    value: number | boolean,
  ) => {
    const itemIndex = facetUiStateArray.findIndex(
      item => item.name === columnName,
    )
    setFacetUiStateArray(
      facetUiStateArray.map((item, index) =>
        index === itemIndex ? { ...item, [propName]: value } : { ...item },
      ),
    )
  }

  const isHidden = (columnName: string) => {
 
    const itemHidden =  facetUiStateArray.find(
      item =>
        item.name === columnName && (item.hidden === 1 || item.isExpanded),
    )
    const result = (itemHidden  !== undefined)
    console.log(`${columnName} is hidden = ${result}` )
    return result
  }

  if (isLoadingNewData || !data) {
    return (
      <div className="SRC-loadingContainer SRC-centerContentColumn">
        {/*
              check loading screen is not undefined or null and show
              it if so
            */}
        {!!loadingScreen && loadingScreen}
        <div>{asyncJobStatus && asyncJobStatus.progressMessage}</div>
      </div>
    )
  } else {
    return (
      <div className="FacetNav">
        <div className="FacetNav__expanded">
          {expandedFacets.length}
          {expandedFacets.map(item => (
            <div
            
         
              key={`facetPanel_${item.index}`}
            >
              <FacetNavPanel
                index={item.index}
                onHide={() => hideExpandedItem(item.facet, item.index)}
                onContract={() => toggleExpand(item.facet, item.index, false)}
                facetToPlot={item.facet as FacetColumnResultValues}
                applyChanges={(
                  facet: FacetColumnResultValues,
                  value: FacetColumnResultValueCount,
                ) =>
                  applyChangesToValuesColumn(
                    request,
                    facet,
                    applyChangesFromQueryFilter,
                    value.value,
                    !value.isSelected,
                  )
                }
              ></FacetNavPanel>
            </div>
          ))}
        </div>
        <div className="FacetNav__row">
          {facets?.map((item, index) => (
            <div
              className="col-sm-12 col-md-6"
              style={{ display: isHidden(item.columnName) ? 'none' : 'block' }}
              key={`facetPanel_${index}`}
            >
              <FacetNavPanel
                index={index}
                onHide={() => hideItem(item.columnName)}
                onExpand={() => toggleExpand(item, index, true)}
                facetToPlot={item as FacetColumnResultValues}
                applyChanges={(
                  facet: FacetColumnResultValues,
                  value: FacetColumnResultValueCount,
                ) =>
                  applyChangesToValuesColumn(
                    request,
                    facet,
                    applyChangesFromQueryFilter,
                    value.value,
                    !value.isSelected,
                  )
                }
              ></FacetNavPanel>
            </div>
          ))}
        </div>
        <div>
          <button
            className="btn FacetNav__showMore"
            onClick={() => setIsShowAll(!isShowAll)}
          >
            {isShowAll ? 'Show Less' : 'Show More'}
          </button>
        </div>
      </div>
    )
  }
}

export default FacetNav
